export const twitter = {
    attrs: { url: {} },
    group: 'block',
    parseDOM: [
        {
            tag: 'div[data-bb-twitter]',
            getAttrs: (dom) => ({
                url: dom.dataset.bbTwitter,
            }),
        },
    ],
    toDOM: (node) => ['div', { 'data-bb-twitter': node.attrs.url }],
};
